.login-form {
  width: 300px;
  background-color: #f1f1f1;
  border-radius: 5px;
  margin: 0 auto 50px;
  padding: 20px;
}

.login-input {
  width: 93%;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 10px;
  padding: 10px;
  display: block;
}

.login-btn {
  width: 100%;
  color: #fff;
  cursor: pointer;
  background-color: #8bc34a;
  border: none;
  border-radius: 4px;
  padding: 10px;
  display: block;
}

.login-btn:hover {
  background-color: #689f38;
}

.login-btn:active {
  background-color: #3e8e41;
}

